
import $ from "jquery";
import CellBox from 'vux/src/components/cell-box/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapActions,mapGetters } from "vuex";
export default {
name: "Yysq",
data() {
return {
msg: "预约申请",
sqlist: [















],
have: false,
show: false,
contitle:"真的要拒绝吗？",
pullupConfig:{
content: '上拉刷新',
autoRefresh: true,
downContent: '够啦够啦',
upContent: '上拉刷新',
loadingContent: '加载中...',
clsPrefix: 'pullupConfig-',
},
sqloadswitch:true,
sqpage:1,
jjbindex:"0",
jjindex:0
};
},
components: {
CellBox,
Confirm ,
Toast,
Scroller
},
methods: {
...mapActions(["action"]),
morebtn(t) {
if (
$(".btnlist")
.eq(t)
.is(":hidden")
) {
$(".btnlist").slideUp();
$(".btnlist")
.eq(t)
.slideDown();
} else {
$(".btnlist")
.eq(t)
.slideUp();
}
},
makeshow(t,k){
this.show=true;
this.jjbindex=t;
this.jjindex=k;
},

alertshow(t,k){//接受预约的方法
this.action({
moduleName:"Jsyy",
url:"Adminapi/Bespeaks/affirm",
method:"post",
data:{
token:this.airforce.login_post.data.token,
id:t
}
}).then((res)=>{
if(res.code==200){
this.$vux.toast.text(res.message)
this.sqlist.splice(k,1);
}else{
this.$vux.toast.text(res.message)
}
}).catch((err)=>{
this.$vux.toast.text(err)
})

},
onConfirm(){//Confirm点击确定,拒绝预约的方法
this.action({
moduleName:"Jjyy",
url:"Bespeaks/reject",
method:"post",
data:{
token:this.airforce.login_post.data.token,
id:this.jjbindex
}
}).then((res)=>{
if(res.code==200){
this.$vux.toast.text(res.message)
this.sqlist.splice(this.jjindex,1);
}else{
this.$vux.toast.text(res.message)
}
}).catch((err)=>{
this.$vux.toast.text(err)
})

},
getmore(){//下拉加载的方法
if(this.sqloadswitch==true){
console.log("已通过申请下拉开关")
this.sqloadswitch=false;
this.action({
moduleName:"Wdyy",
url:"Adminapi/Bespeaks/lists",
method:"post",
data:
{
token:this.airforce.login_post.data.token,
type:"2",
page:this.sqpage.toString(),
pagesize:"9",
status:"0"
}
}).then((res)=>{
if(res.code==200){
this.sqlist=this.sqlist.concat(res.data.lists);
if(res.data.lists.length<5){
this.sqloadswitch=false;
}else{
this.sqloadswitch=true;
}
console.log("页码",this.sqpage,"数据条数",this.sqlist.length)
this.sqpage++;
}else{
this.$vux.toast.text(res.message)
}
}).catch((err)=>{
this.$vux.toast.text(err)
})
}else{
return false;
}
}
},
computed:{
...mapGetters(["airforce"]),
},
created(){
this.action({//页面进入初始渲染待进行列表
moduleName:"Wdyy",
url:"adminapi/Bespeaks/lists",
method:"post",
data:
{
token:this.airforce.login_post.data.token,
type:"2",
page:this.sqpage.toString(),
pagesize:"9",
status:"0"
}
}).then(res=>{
console.log(res)
if(res.code==200){
this.sqlist=this.sqlist.concat(res.data.lists);
if(this.sqlist.length==0){
this.have=true;
}else if(res.data.lists.length<9){
this.sqloadswitch=false;
}
console.log(this.sqlist)
this.sqpage++;
}else{
this.sqloadswitch=false;
this.have=true;
this.$vux.toast.text(res.message);
}
console.log(this.sqlist.length)
}).catch(err=>{
this.have=true;
this.$vux.toast.text(err);
})
}
};



