
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { mapGetters } from 'vuex'
import { ReleaseBox } from '@/components'
export default {
name: 'layou-footer',
components: { Flexbox, FlexboxItem, ReleaseBox },
data () {
return {
releaseIconSelect: false,
menuList: [
{
title: '首页',
name: 'Home',
icon: '&#xe655;',
type: 1,
link: '/Home'
},
{
title: '分享',
name: 'Share',
icon: '&#xe6b2;',
type: 1,
link: '/Share'
},






{
title: '消息',
name: 'Message',
icon: '&#xe64c;',
type: 1,
link: '/Message'
},
{
title: '个人中心',
name: 'User',
icon: '&#xe633;',
type: 1,
link: '/User'
}
]
}
},
methods: {
release () {
this.releaseIconSelect = !this.releaseIconSelect
},
go (item) {
if (item.link) {
this.$router.push(item.link)
}
}
},
computed: {
...mapGetters(['airforce'])
}
}



