
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'

import '@/assets/js/plus.js'

import * as filters from './filters/index'
Object.keys(filters).forEach(key => {
Vue.filter(key, filters[key])
})


store.registerModule('vux', {
state: {
isLoading: false
},
mutations: {
updateLoadingStatus (state, payload) {
state.isLoading = payload.isLoading
}
}
});


function plusInit(dev,build) {
if($$H5plus == "dev"){
dev();
}else {
build();
};
}

router.beforeEach(function (to, from, next) {

store.commit('AIRFORCE_DO', {
data: {
moduleName: 'layout',
goods: _.merge({

left:'&#xe60a;',

leftSpan:'50',

title: to.name || '',

titleShowType: false,

right:'&#xe668;',

rightSpan:'50',

rightFontSize:16,

rightFontScale:1,

headerShow:true,

footerShow:true,

layoutMainType:'',

type:'1',

clickLeftBack:true,

clickLeft:new Function(),

clickContent:new Function(),

clickRight:new Function(),

bodyColor:"#f2f0f5",

menuName:null,


searchShow:false,

search:new Function(),

searchOnClose:new Function(),

searchText:'',

}, to.meta)
}
});
try {

if (window.scrollTo) {
window.scrollTo({
y: 0
});
if(document.querySelector){
var html = document.querySelector("html");
html.scrollTop = 0;
}
}
if(document.body.scrollTop){
document.body.scrollTop = 0;
}
}catch (e){}

document.title = store.state.airforce.layout.title;

document.body.style.backgroundColor = store.state.airforce.layout.bodyColor;
try {

if (!store.state.airforce.login_post && localStorage.login_post) {
store.commit('AIRFORCE_DO', {
data: {
moduleName: 'login_post',
goods: JSON.parse(localStorage.login_post)
}
})
}
} catch (e) {}

store.commit('AIRFORCE_DO', {
data: {
moduleName: 'change',
goods: {
set: (e, a, moduleName) => {
if(moduleName){
store.commit('AIRFORCE_DO', {
data: {
moduleName: moduleName,
goods: _.set({}, a, e)
}
})
}else {
store.commit('AIRFORCE_DO', {
data: {
moduleName: a,
goods:e
}
})
}
}
}
}
});

store.commit('updateLoadingStatus', {isLoading: true});







const whiteList = ['/Login', '/Register', '/ForgetPwd','/AccountAppeal','/ApplicationDetails','/AntPushShop','/GuidePage'];

if (store.state.airforce.login_post && store.state.airforce.login_post.code == 200) {



next() 

} else {
if (whiteList.indexOf(to.path) !== -1) { // 在免登录白名单，直接进入
next();
} else {

store.commit('AIRFORCE_DO', {
data: {
moduleName: 'layout',
goods:from.meta
}
});

store.commit('updateLoadingStatus', {isLoading: false});
next('/Login');










}
}
})
router.afterEach(function (to) {

store.commit('updateLoadingStatus', {isLoading: false})
});

Vue.config.productionTip = false

import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(AlertPlugin)
Vue.use(ToastPlugin)
Vue.use(ConfirmPlugin)
Vue.use(DatetimePlugin)
Vue.use(LoadingPlugin)


import UtilsPlugin from "./utils/index"
Vue.use(UtilsPlugin)



import { ZLoadingPlugin } from "./components/index"
Vue.use(ZLoadingPlugin)


import plusPlugin from "@/assets/js/plusPlugin.js"
Vue.use(plusPlugin)



import useStore from "./store/useStore"
Vue.use(useStore)

import vueg from 'vueg'
import 'vueg/css/transition-min.css'
const options={
duration: '0.4', //转场动画时长，默认为0.3，单位秒
firstEntryDisable: false, //值为true时禁用首次进入应用时的渐现动画，默认为false
firstEntryDuration: '.6', //首次进入应用时的渐现动画时长，默认为.6
forwardAnim: 'fadeInRight', //前进动画，默认为fadeInRight
backAnim: 'fedeInLeft', //后退动画，默认为fedeInLeft
sameDepthDisable: false, //url深度相同时禁用动画，默认为false
tabs: [], //默认为[]，'name'对应路由的name,以实现类似app中点击tab页面水平转场效果，如tabs[1]到tabs[0]    ，会使用backAnim动画，tabs[1]到tabs[2]，会使用forwardAnim动画
tabsDisable: false, //值为true时，tabs间的转场没有动画，默认为false
shadow:false, //值为false，转场时没有阴影的层次效果
disable: false, //禁用转场动画，默认为false，嵌套路由默认为true
nuxt: false 
}
Vue.use(vueg, router,options) 

function init() {
/* 实例化 */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
});
};
init();















