
import { mapActions, mapGetters } from 'vuex'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import ZPopupItem from './z-popup-item.vue'
export default {
name: "z-popup",
components:{ Cell, XButton, Popup, ZPopupItem },
props:{
moduleName:{
type:String,
default:'zPopup'
},
fieldName:{
type:String,
default:'zPopup'
},
list:{
type:Array,
default:()=>[]
},
title:{
type:String,
default:''
},
placeholder:{
type:String,
default:''
},
alert:{
type:String,
default:''
},
},
data(){
return {
popupShow:false,
popupSelectData:{}
}
},
methods:{
...mapActions(['action']),
onShow(){
this.popupShow = true;
},
close(){
this.popupShow = false;
},
onChange(val,key){
this.popupSelectData[key] = null;
this.popupSelectData = _.merge(this.popupSelectData,val);
},
setData(){
this.action({
moduleName:this.moduleName,
goods:_.set({},this.fieldName,null)
});
this.action({
moduleName:this.moduleName,
goods:_.set({},this.fieldName,this.popupSelectData)
});
}
},
computed:{
...mapGetters(['airforce']),
value(){
if(this.airforce[this.moduleName] && this.airforce[this.moduleName][this.fieldName]){
let data = this.airforce[this.moduleName][this.fieldName];
let index = 0;
for(let  k in data){
if(data[k].length > 0){
index += data[k].length;
};
};
if(index > 0){
return index;
}
return this.placeholder;
}
return this.placeholder;
}
},
watch:{
popupShow(val){
if(!val){
this.setData();
}
},
list(val){
let keys = Object.keys(this.popupSelectData);
keys.forEach(e=>{
if(val.indexOf(parseInt(e)) < 0 ){
delete this.popupSelectData[e];
}
});
this.setData();
}
}
}



