
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popover from 'vux/src/components/popover/index.vue'

import { mapGetters, mapActions } from  "vuex"
import ZSearch from '../../components/z-search'
export default {
name: "layout-header",
components:{ Flexbox, FlexboxItem, XInput, Group, Popover, ZSearch },
data(){
return {
scrollBool:'',
Time:0,
scrollY:0,
showSearch:false,
}
},
computed:{
...mapGetters(['airforce']),
leftSpan(){
if(this.airforce.layout.leftSpan){
return this.airforce.layout.leftSpan;
}
return '50';
},
rightSpan(){
if(this.airforce.layout.rightSpan){
return this.airforce.layout.rightSpan;
}
return '50';
},
showSearchInit(){
if(!this.airforce.showSearch){
this.showSearch = false;
}
}
},
methods:{
...mapActions(['action']),
goback(){
if(this.airforce.layout.left == '&#xe60a;'){
this.$router.back();
}
},
clickLeft(){
if(!this.airforce.layout.clickLeftBack){
this.airforce.layout.clickLeft();
return;
}
switch (this.airforce.layout.type){
case "1":
this.goback();
break;
case "2":
this.goback();
break;
case "3":
this.goback();
break;
case "4":
if(this.airforce.layout.searchShow){
this.action({
moduleName:'layout',
goods:{
searchShow:false
}
});
}else {
this.goback();
}
break;
default:
this.airforce.layout.clickLeft();
break
}
},
clickContent(){
this.airforce.layout.clickContent();
},
clickRight(){
if(this.airforce.layout.right == '&#xe668;' && this.airforce.layout.type != "4"){
this.$router.push('/Message');
return;
}
if(this.airforce.layout.type && this.airforce.layout.type == "4"){
if(this.airforce.layout.searchShow){
this.action({
moduleName:'layout',
goods:{
searchShow:false
}
});
this.search(this.airforce.layout.searchText);
}else {
this.action({
moduleName:'layout',
goods:{
searchShow:true
}
})
}
}
this.airforce.layout.clickRight();
},
search(val,_vm){
this.airforce.layout.search(val,_vm);
},
onClose(val,select){
this.airforce.layout.searchOnClose(val,select);
},
showSearchFun(){
this.showSearch = true;
},
calc(){
this.scrollY = window.scrollY;
if(window.scrollY <= 5){
this.scrollBool = '';
return;
}
this.scrollBool = 'scrollBool';
},
init(){
this.calc();
window.addEventListener('touchstart',()=>{
this.calc();
});
window.addEventListener('touchmove',()=>{
this.calc();
});
window.addEventListener('touchend',()=>{
for(let i = 1 ; i < 100; i++){
setTimeout(()=>{
this.calc();
},30*i);
}
});
}
},
mounted(){
this.init();
},
watch:{
$route(){
this.showSearch = false;
},
showSearch(val){
this.action({
moduleName:"showSearch",
goods:val
});
}
}
}



