
import XCircle from 'vux/src/components/x-circle/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import { setTimeout } from 'timers';
export default {
name:"Rents",
data(){
return{
msg:"收租",
csbfb:0,//圆饼图的初始百分比（0）
bfb:81,//圆饼图的百分比
zjall:1800,//本月应收租金
}
},
components:{
XCircle ,
CellBox
},
methods:{
gotx(){
this.$router.push("/Tx")
}
},
created(){
let time=setInterval(()=>{
if(this.csbfb==this.bfb){
clearInterval(time);
}else{
this.csbfb+=1;
}
},10)

}
}



