
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'

export default {
name: "guide-page",
components:{ Swiper, SwiperItem, XImg },
data(){
return {
img: "http://placeholder.qiniudn.com/60x60/e5e5e5/e5e5e5"
}
},
computed:{
GuidePageImgs(){
try {
if(this.airforce.configs_post.data){
const resData =  this.airforce.configs_post.data.filter(e=>{
return e.type == '13';
});
return resData;
}
}catch (e){}
return [];
},
},
methods:{
swiperItem(index){
if(index == this.GuidePageImgs.length){
this.plus.then(plus=>{
plus.storage.setItem("launchFlag","true");
});
this.$router.back();

}
}
},
mounted(){
this.plus.then(plus=>{
plus.navigator.setFullscreen( true );
});
}
}



