
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: "z-search",
components:{ Flexbox, FlexboxItem, XInput, Cell, Group },
data(){
return {
upshow:false,
values:'',
}
},
methods:{
clickInput(item,type){
setTimeout(()=>{
this.upshow = false;
setTimeout(()=>{
try {
this.$vnode.data.model.callback(false);
}catch (e){}
if(type == "select"){
this.$emit('on-close',this.values,item);
}else {
this.$emit('on-close',this.values,null);
}
},500);
});
},
enter(val){
this.$emit('on-enter',val,this);
},
change(val){
this.$emit('on-change',val,this);
},
clear(){
this.$emit('on-enter');
}
},
computed:{
up(){
try {
let data = this.$utils.SearchParentKey(this.$parent,this.$vnode.data.model.expression);
if(data.parent){
setTimeout(()=>{
this.upshow = data.value;
});
return data.value;
}
}catch (e){}
return false;
},
list(){
try {
return this.airforce.SearchData.data.data;
}catch (e){}
return [];
}
},
}



