
import VueCoreImageUpload from 'vue-core-image-upload'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'

import { mapActions, mapGetters } from 'vuex'
export default {
name: "images-upload",
props:{
type:{
type:Number,
default:0
},
title:{
type:String,
default:null
},
text:{
type:String,
default:''
},
text2:{
type:String,
default:null
},
toastText:{
type:String,
default:null
},
name:{
type:String,
default:null
},
data:{
type:Object,
default:function () {
return {}
}
},
maxFileSize:{
type:Number,
default:10010485760
},
url:{
type:String,
default:`${$$rootUrl}app/installment/upfile`
},
src:{
type:String,
default:require("@/assets/img/Housing/upload.png")
},
imgSrc:{
type:String,
default:null
},
credentials:{
type:Boolean,
default:false
},
inputOfFile:{
type:String,
default:'file'
},
ossUrl:{
type:String,
default:`app/installment/upcardphone`
},
orderNo:{
type:String,
default:null
},
prefix:{
type:String,
default:"upload_"
},
off:{
type:String,
default:null
},
isxhr:{
type:Boolean,
default:true
},
isUpdata:{
type:Boolean,
default:false,
},
alert:{
type:Boolean,
default:false,
}
},
data(){
return {
percentIndex:0,
time:0,
base64:null,
base64Url:null,
base64Url2:[],
isImgs:false,
}
},
methods: {
...mapActions(['action']),
delImg(item,index){
this.base64Url2.splice(index,1);
this.$emit('imagechanged',this.base64Url2,true,index);
},
imageuploaded(res) {
clearInterval(this.time);
if (res.code == 200) {
this.base64Url = this.base64;
this.base64 = null;
this.percentIndex = 100;
this.$root.$children[0].imgZoomIn();
if(this.toastText){
this.$vux.toast.show({
text:`【${this.toastText}】上传成功`,
type:"text",
width:"80%",
position:"bottom",
});
}
if(this.type != 2){
this.action({
moduleName:'homeSubmit',
goods:_.set({},this.prefix+this.inputOfFile,{
bool:true,
base64Url:this.base64Url,
})
});
};
if(this.ossUrl){
this.action({
moduleName:"OSSupcardphone",
method:"post",
url:this.ossUrl,
data:{
...this.newData,
type:this.inputOfFile,
},
isFormData:true,
});
};
}else {
this.base64Url2.pop();
this.percentIndex = 0;
if(this.toastText) {
this.$vux.toast.show({
text: `【${this.toastText}】上传失败<br>${res.message}`,
type: "text",
width: "80%",
position: "bottom",
});
}
if(this.name) {
this.action({
moduleName: 'homeSubmit',
goods: _.set({},this.prefix+this.inputOfFile, undefined)
});
}
}
},
errorhandle(){
this.base64Url2.pop();
clearInterval(this.time);
this.percentIndex = 0;
if(this.toastText) {
this.$vux.toast.show({
text: `【${this.toastText}】上传失败`,
type: "text",
width: "80%",
position: "bottom",
});
}
if(this.name) {
this.action({
moduleName: 'homeSubmit',
goods: _.set({},this.prefix+this.inputOfFile, undefined)
});
}
},
imageuploading(e){
this.toUrl(e,base64=>{
this.base64 = base64;
});
let index = 0;
this.time = setInterval(()=>{
if(index < 96){
index += 3;
this.percentIndex = index;
}else {
clearInterval(this.time);
}
},100)
},
toUrl(e,fn){
let file = new FileReader();
file.readAsDataURL(e);
file.onload = d => {
let base64 = d.target.result || d.currentTarget.result || d.srcElement.result;
fn(base64);
};
},
imagechanged(e){
this.$emit('imagechanged',e);
if(this.type == 2){
this.isImgs = true;
if(!e.length){
this.toUrl(e,base64=>{
this.base64Url2.push(base64);
});
this.action({
moduleName: 'homeSubmit',
goods: _.set({},this.prefix+this.inputOfFile, this.base64Url2)
});
return;
}
for(let i = 0 ; i < e.length ; i++){
this.toUrl(e[i],base64=>{
this.base64Url2.push(base64);
});
}
this.action({
moduleName: 'homeSubmit',
goods: _.set({},this.prefix+this.inputOfFile, this.base64Url2)
});
}
}
},
computed: {
...mapGetters(['airforce']),
imgSrcNew(){
if(this.type == 2 && this.base64Url2){
return this.base64Url2;
}
if(this.base64Url){
return this.base64Url;
}
if(this.imgSrc){
this.percentIndex = 100;
return this.imgSrc;
}
return this.src;
},
newData(){
let orderid = '';
try {
if(this.$router.currentRoute.query.editor == "true"){
orderid = this.airforce.getperiods.data.orderid
}else {
orderid = this.airforce.home_post.data.orderid;
}
}catch (e){}
let retData = {};
try {
retData = {
uid:this.airforce.login_post.data.uid,
token:this.airforce.login_post.data.token,
}
if(this.orderNo){
retData.order_no = this.orderNo;
}
if(orderid.length > 0){
retData.orderid = orderid;
}
} catch (e){}
for(let i in this.data){
retData[i] =this.data[i];
}
return retData;
}
},
components:{
VueCoreImageUpload, Flexbox, FlexboxItem, XProgress
},
mounted(){
try {
let base64Url = this.airforce.homeSubmit[this.prefix+this.inputOfFile];
if(base64Url){
if(base64Url.base64Url && base64Url.bool){
this.base64Url = base64Url.base64Url;
}
if(this.type == 2){
this.isImgs = true;
this.base64Url2 = base64Url;
}
this.percentIndex = 100;
}
}catch (e){}
if(this.off){
this.action({
moduleName:'homeSubmit',
goods:_.set({},this.prefix+this.inputOfFile,{
bool:true,
base64Url:this.off,
})
});
}
this.base64Url2 = [];
},
}



