
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Panel from 'vux/src/components/panel/index.vue'

import { mapActions, mapGetters } from 'vuex'
export default {
name: "my-contacts",
components: { Tab, TabItem, Swiper, SwiperItem, Flexbox, FlexboxItem, Panel },
data(){
return {
tabs:[
{
title:'亲密好友',
list:[
{
src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
fallbackSrc: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: '张三',
desc: '由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。',
}
]
},
{
title:'我的好友',
list:[]
}
],
index: 0,
}
},
methods:{
...mapActions(['action']),
seeMessage(item){
switch (item.type){
case '2':
this.$router.push('/BookingDetails')
break;
default:
this.$router.push('/MessageDetails?id='+item.id)
break;
}
},
getList(cons={}){
let conInit = {
token:this.airforce.login_post.data.token,
status:1,
};
this.action({
moduleName:'MessageList_post',
url:'/adminapi/member/record',
method:"post",
data:_.merge(conInit,cons)
}).then(res=>{
try {
this.tabs[conInit.status-1].list = res.data.data
}catch (e){}
})
}
},
computed:{
...mapGetters(['airforce']),
},
mounted(){




}
}



