
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { mapActions } from 'vuex'
export default {
name: "z-housing-card",
components:{ Flexbox, FlexboxItem },
props:{
list:{
type:Array,
default:()=>[],
}
},
data(){
return {
defaultImg:require('@/assets/img/Login/touxiang.png')
}
},
methods:{
...mapActions(['action']),
onClick(item){
if(item.onClick){
item.onClick(item);
return;
}
if(item.link){
this.$router.push(item.link);
return;
}
this.action({
moduleName:'HousingDetailsSelect',
goods:item,
});
this.$router.push("/HousingDetails?id="+item.id+"&o_id="+item.o_id);
}
}
}



