
import { mapActions, mapGetters } from 'vuex'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Card from 'vux/src/components/card/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { ReleaseBox, ZCard } from "@/components"
export default {
name: "housing-resource",
components:{ ReleaseBox, ZCard, Swiper, SwiperItem, Tab, TabItem, Flexbox, FlexboxItem, Card, Scroller, LoadMore },
data(){
return {
ReleaseBoxShow:false,
index:0,
page0:1,
page1:1,
page2:1,
page3:1,
tabs:[
{
title:'已出租',
list:[],
},
{
title:'未发布',
list:[],
},
{
title:'已发布',
list:[],
},
{
title:'即将/已到期',
list:[],
},
],
pullupConfig:{
content: '上拉刷新',


autoRefresh: true,
downContent: '放开我',
upContent: '上拉刷新',
loadingContent: '加载中...',
clsPrefix: 'pullupConfig-',
},
}
},
methods:{
...mapActions(['action']),
getList(conf = {}){
let data = _.merge({
token:this.airforce.login_post.data.token,
o_id:this.airforce.login_post.data.id,
status:'0',
},conf);
return this.action({
moduleName:'getHousesourceLists_post_'+data.status,
method:'post',
url:'/adminapi/Housesource/lists',
data:data
}).then(res=>{
if(res.code != 200){
this.$vux.toast.text(res.message);
return;
};
try {
var index = 0;
switch(data.status){
case '0':
index = 1;
break;
case '1':
index = 0;
break;
case '2':
index = 2;
break;
default:
index = 3;
break;
};
this['page'+index] += 1;
this.tabs[index].list = this.tabs[index].list.concat(res.data.data);
}catch (e){
this.tabs[index].list = [];
}
return res;
}).catch(err=>{
this.$vux.toast.text(err);
});
},
pullup(str,index){
let status = '1';
switch (index){
case 0:
status = '1';
break;
case 1:
status = '0';
break;
case 2:
status = '2';
break;
default:
status = '3';
break;
};
var obj = this.$refs[str][0];
this.getList({
status:status,
page:this['page'+index],
}).then(res=>{
this.$nextTick(() => {
try {
if(res.data.data.length == 0){
const pullup = obj.pullup;
pullup.userConfig.content = "到底啦";
pullup.userConfig.upContent = "到底啦";
pullup.xscroll.off("scrollend", pullup._scrollEndHandler, pullup);
pullup.xscroll.off("scroll", pullup._scrollHandler, pullup);
pullup.xscroll.off("pan", pullup._panHandler, pullup);
pullup.xscroll.boundry.resetBottom();
pullup.__isRender = false;
pullup._evtBinded = false;
};
}catch (e){}
obj.donePullup();
});
}).catch(()=>{
try {
obj.donePullup();
}catch (e){}
});

}
},
computed:{
...mapGetters(['airforce']),
},
mounted(){
this.action({
moduleName:'layout',
goods:{
clickRight:()=>{
this.ReleaseBoxShow = true;
}
}
});

this.getList({status:'1'});

this.getList();

this.getList({status:'2'});

this.getList({status:'3'});
}
}



