
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import CheckUpdate from '@/components/CheckUpdate.vue'
import { mapActions } from 'vuex'
export default {
name: 'about',
...mapActions,
computed: {
version () {
try {
if (this.airforce.app_info.version) {
return this.airforce.app_info.version
}
} catch (e) {}
return '0.0.1'
},
isNewV () {
try {
if (this.airforce.app_info.isNewV) {
return this.airforce.app_info.isNewV
}
} catch (e) {}
return false
}
},
data () {
return {
logo: require('@/assets/img/Login/logo.png'),
update: false
}
},
methods: {
...mapActions(['action']),
goAbout () {
this.update = true
setTimeout(() => {
this.update = false
}, 500)
}
},
components: {
Group,
Cell,
CellBox,
XImg,
Badge,
CheckUpdate
},
mounted () {
this.action({
moduleName: 'app_info',
goods: {}
})
if (window.plus && window.getWgtVer) {
window.getWgtVer((info) => {
this.action({
moduleName: 'app_info',
goods: info
})
})
}
}
}



