
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'

import {mapActions} from "vuex"
export default {
name: "Jd",
data() {
return {
msg: "租房借贷",
now: 20000,
all: 20000
};
},
components: {
Group,
Cell,
Marquee,
MarqueeItem
},
methods: {
...mapActions(["action"]),
takemoney() {
this.$router.push("/Jq");
}
},
created() {
this.now = parseFloat(this.now).toLocaleString();
this.all = parseFloat(this.all).toLocaleString();
console.log(this.now);
},
mounted(){
this.action({
moduleName:'layout',
goods:{
clickRight:()=>{
this.$router.push("/Edsz")
}
}
})
}
};



