
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import { mapActions } from "vuex"
export default {
name:"Sz",
data(){
return{
msg:"设置",
show:false,
menus:[
{
label: "确定退出登陆吗？",
type: 'info'
}, {
label: '确定',
type: 'warn'
}
]
}
},
components:{
Group,
Cell,
Actionsheet
},
methods:{
...mapActions(['action']),
makeshow(){
this.show=true;
},
sureexit(){//退出登陆的方法
this.$utils.Logout(this);
}
}
}



