
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name: "z-pull-scroller",
components:{ Scroller, LoadMore},
data(){
return {
load:false,
loadText:"正在加载",
loadEnd:false,
loadEndText:"到底啦",
loadStart:true,
loadStartText:"下拉加载更多",

}
},
props:{
height:{
type:String,
default:'300px'
},
bottom:{
type:Number,
default:20
}
},
methods:{
reset(con = {}){
this.$refs.scrollerBottom.reset(con);
},
onScrollBottom(){




this.$emit("change",this);

},
}
}



