
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: "income-details",
components:{ Group, Cell },
data(){
return {
list:[]
}
},
mounted(){
try {
this.action({
moduleName:"layout",
goods:{
title:this.airforce.selectIncomeDetails.docTitle,
}
});
}catch (e){}
this.$vux.toast.text("没有更多数据了！")
}
}



