
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: "z-scroller",
components:{ Scroller, Flexbox, FlexboxItem },
data(){
return {
bool:false,
}
},
props:{
title:{
type:String,
default:null,
},
type:{
type:String,
default:'1',
},
list:{
type:Array,
default:()=>[],
/*
{
img:'imgSrc',//img字段没有则显示背景颜色color
text:"sdas",
color:"#f00",//背景颜色
textColor:"#f00",
minTitle:"sdas",
money:1860,
location:'高新区',
name:'江南一品',
type:'四室一厅合租',
}
* */
},
width:{
type:Number,
default:200,
},
height:{
type:Number,
default:100,
},
marginLeft:{
type:Number,
default:15,
}
},
computed:{
newHeight(){
if(this.type == '2'){
return this.height + 30;
}
if(this.type == '3'){
return this.height + 100;
}
return this.height;
},
listWidth(){
return this.list.length * this.width + this.list.length * this.marginLeft;
},
},
methods:{
click(item){
if(item.link){
this.$router.push(item.link);
return;
}
if(item.click && typeof item.click == "function"){
item.click(item);
};
this.$emit("item-click",item);
},
onClick(){
this.$emit("on-click",this);
},
onScroll(e){

},
getXY(){
let supportPageOffset = window.pageXOffset !== undefined;
let isCSS1Compat = ((document.compatMode || "") === "CSS1Compat");

let x = supportPageOffset ? window.pageXOffset : isCSS1Compat ? document.documentElement.scrollLeft : document.body.scrollLeft;
let y = supportPageOffset ? window.pageYOffset : isCSS1Compat ? document.documentElement.scrollTop : document.body.scrollTop;
return {x:x,y:y};
}
},
mounted(){
let evXstart = 0;
let evX = 0;
let evYstart = 0;
let evY = 0;
let max = 5;
this.$el.addEventListener('touchstart',ev=>{
evXstart = ev.touches[0].clientX;
evYstart = ev.touches[0].clientY;
},false);
this.$el.addEventListener('touchmove',ev=>{
evX = ev.touches[0].clientX - evXstart;
evY = ev.touches[0].clientY - evYstart;
if(Math.abs(evX) < max){

this.bool = true;
}else {
setTimeout(()=>{
this.bool = false;
},1500)
}
},false);
this.$el.addEventListener('touchend',()=>{
setTimeout(()=>{
this.bool = false;
},1500)
},false);
}
}



