
import Loading from 'vux/src/components/loading/index.vue'

import { mapState, mapActions, mapGetters } from 'vuex'
import Hammer from 'hammerjs'
import { ReleaseBox } from '@/components'
export default {
name: 'App',
components: { Loading, ReleaseBox },
data () {
return {
isRouterAlive: true,
notice: false,
noticeUrl:null,
}
},
methods: {
reload () {
this.isRouterAlive = false
this.$nextTick(() => (this.isRouterAlive = true))
},

notWhiteListBack (event) {
var callback = new Function()
if (typeof event === 'function') {
callback = event
};

const whiteList = ['Home', 'User', 'Login', 'Message', 'Share','GuidePage','AntPushShop'];
if (!whiteList.some(title => { return title == this.airforce.layout.menuName })) {
this.$router.back()
} else {
callback()
};
},

getInntData (confs = {}) {
let configs = {
moduleName: 'configs_post',
url: '/header/daohang',
method: 'post',
data: {

}
}
for (let i in confs) {
if (i == 'data') {
for (let j in confs[i]) {
configs[i][j] = confs[i][j]
}
} else {
configs[i] = confs[i]
}
}
return this.action(configs).then(res => {
return res
}).catch(err => {
return err
})
},
initConfig () {
this.$vuz.loading.show()
this.getInntData().then(res => {
this.$vuz.loading.hide()
}).catch(err => {
this.$vuz.loading.hide()
})
},
},
computed: {
...mapGetters(['airforce']),
...mapState({
isLoading: state => state.vux.isLoading
}),
isBan(){
try {
if(this.airforce.configs_post.data){
var Data = _.find(this.airforce.configs_post.data,{type:'14'})
if(Data.url == "on"){
return true;
}
return false;
}
}catch (e){}
return false;
}
},
created () {
this.plus.then(plus=>{
plus.navigator.setFullscreen( false );
});
this.$vux._this = this
window.VUX = this.$vux

const whiteList = ['/', '/ForgetPwd', '/Register', '/AccountAppeal', '/ApplicationDetails'];
if (!whiteList.some(title => { return title == this.$route.path })) {
this.initConfig()
}

},
mounted () {
var time = setInterval(() => {

if (window.checkUpdate) {


























if (window.backListener && window.backListener.start) {
window.backListener.start(() => {
this.$vux.loading.hide()
if (this.airforce.showSearch) {
this.action({
moduleName: 'showSearch',
goods: false
})
return
}
this.notWhiteListBack(() => {
if (this.confirmBack > 0) {
plus.runtime.quit()
return
};
this.$vux.toast.show({
text: '再次点击退出',
type: 'text',
width: 'auto',
position: 'bottom'
})
this.confirmBack += 1
setTimeout(() => {
this.confirmBack = 0
}, 2500)
})
})
};
clearInterval(time)
}
}, 200)
setTimeout(() => {
clearInterval(time)
}, 10000);

var h = new Hammer(this.$refs.appView);
h.on('swiperight',(e) =>{
this.notWhiteListBack(e);
});
}
}



