
import CellBox from 'vux/src/components/cell-box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapActions,mapGetters } from "vuex";
import { setTimeout } from 'timers';
export default {
name:"Addyhk",
data(){
return{
msg: "添加银行卡",
time:60,
yzmvaule:"发送验证码",
num:"",
numyz:/^([1-9]{1})(\d{14}|\d{18})$/,
name:"",
tel:"",
yzm:"",
code:""
}
},
components:{
CellBox,
Toast
},
methods:{
...mapActions(["action"]),
save(){//点击保存银行卡信息
if(this.num==""){
this.$vux.toast.text("请输入银行卡");
return false;
}
if(!this.numyz.test(this.num)){
this.$vux.toast.text("请输入正确的银行卡号");
return false;
}

if(this.name==""){
this.$vux.toast.text("请输入银行名称");
return false;
}

if(this.tel==""){
this.$vux.toast.text("请输入手机号");
return false;
}
if(this.yzm==""){
this.$vux.toast.text("请输入验证码");
return false;
}
if(this.yzm==this.code){
this.action({
moduleName:"addbank",
url:"adminapi/member/addbank",
method:"post",
data:{
token:this.airforce.login_post.data.token,
bank:this.num,
bank_name:this.name,
bank_phone:this.tel,
code:this.yzm
}
}).then((res)=>{
if(res.code==200){
this.$vux.toast.text(res.message)
setTimeout(()=>{
this.$router.back();
},2000)
}else{
this.$vux.toast.text(res.message)
}
}).catch((err)=>{
this.$vux.toast.text(err)
})
}else{
this.$vux.toast.text("请输入正确的验证码");
return false;
}

},
send(){//发送验证吗的方法
if(this.yzmvaule=="发送验证码"){
this.action({
moduleName:"getcode",
url:"adminapi/member/getcode",
method:"post",
data:{
phone:this.tel
}
}).then((res)=>{
if(res.code==200){
let that=this;
let asd= setInterval(function bcv(){
that.time-=1;
that.yzmvaule=that.time+"s后再次发送";
if(that.time == 0){
that.yzmvaule="发送验证码";
that.time=60;
clearInterval(asd);
}
},1000)
this.code=res.data.code_name;
this.$vux.toast.text("验证码发送成功");
}else{
this.$vux.toast.text(res.message)
}
}).catch((err)=>{
this.$vux.toast.text(err)
})
}
}
},
computed:{
...mapGetters(["airforce"]),
},
created(){

}
}



