
import $ from "jquery";
import CellBox from 'vux/src/components/cell-box/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
name: "Zhgl",
data() {
return {
msg: "租户管理",
jzscreen: ["全部", "今日到期", "三日到期", "七日到期"],
have: false,
zhlist: [1, 2, 3, 4, 5, 6, 7, 8, 9, 0],
show: false,
menus: [
{
label: "确定删除租约吗？",
type: "info"
},
{
label: "确定",
type: "warn"
}
],
animates:true
};
},
components: {
CellBox,
Radio,
Group,
Actionsheet
},
methods: {
disappear(){//点击其他地方收起下拉框

if(!$(".hyscreen").is(":hidden")){
$(".hyscreen").slideUp();
$(".jzscreen").slideUp();
}
if(!$(".jzscreen").is(":hidden")){
$(".jzscreen").slideUp();
$(".hyscreen").slideUp();
}
},
jzchange(value, label) {

$(".jzscreen").slideToggle();
},
hychange(value, label) {

$(".hyscreen").slideToggle();
},
makejz() {
$(".hyscreen").css("display", "none");
$(".jzscreen").slideToggle();

},
makehy() {
$(".jzscreen").css("display", "none");
$(".hyscreen").slideToggle();
},
morebtn(t) {

if (
$(".btnlist")
.eq(t)
.is(":hidden")
) {
$(".btnlist").slideUp();
$(".btnlist")
.eq(t)
.slideDown();
} else {
$(".btnlist")
.eq(t)
.slideUp();
}
if(this.animates==true){
$(".more span").css({
'animation': 'rotatefresh 5s infinite linear'
});
console.log(this.animates)
this.animates=!this.animates;
}else{
$(".more span").css({
'animation': 'rotatefresh 5s infinite linear'
});
this.animates=!this.animates;
}
},
makeshow() {

this.show = true;
},
delzy() {

console.log("好了，这条已经被删除了");
}
}
};



