
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'

import utils from "@/utils/utils.js"
export default {
name: "check-update",
components:{
XDialog, XProgress
},
data(){
return {
percent:0,
show:false,
}
},
props:{
update:{
type:Boolean,
default:false,
}
},
methods:{
init(){
window.checkUpdate({
callback:()=>{
this.show = true;
var time = setInterval(()=>{
if(this.percent >= 100){
clearInterval(time);
this.show = false;
return;
}
if(this.percent < 99){
this.percent += 1;
}
},300);
},
err:()=>{
this.show = false;
},
ok:()=>{
this.percent = 100;
this.show = false;
}
});
}
},
computed:{
newUpdate(){
if(this.update){
if(utils.isIosAndroid()){
this.$vux.alert.show(
{
title:"软件更新提示",
content:"暂不支持ios系统升级,如需升級请联系开发人员",
}
);
return;
};
this.init();
}
}
}
}



