
import Cell from 'vux/src/components/cell/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapGetters, mapActions } from "vuex"
export default {
name: "z-select",
components:{
Cell, PopupPicker, Flexbox, FlexboxItem, XInput, Group
},
props:{
data:{
type:Array,
default:function () {
return [];
},
},
selectPlaceholder:{
type:String,
default:'请选择',
},
placeholder:{
type:String,
default:'',
},
title:{
type:String,
default:null,
},
type:{
type:String,
default:null
},
name:{
type:String,
default:'',
required: true
},
moduleName:{
type:String,
default:null,
required: true
},
valueAlign:{
type:String,
default:"left",
},
labelWidth: String,
},
data(){
return {
selectShow:false,
}
},
methods:{
...mapActions(["action"]),
select(){
this.selectShow = true;
},
selectHide(e){
this.selectShow = false;
},
selectChange(e){
let findObj = _.find(this.data,o=>{
return o.value == e[0];
});
this.action({
moduleName:this.moduleName,
goods:_.set({},`${this.name}_Select`,findObj)



});
this.action({
moduleName:this.moduleName,

goods:_.set({},`${this.name}_SelectVal`,[findObj.value])
});
this.action({
moduleName:this.moduleName,
goods:_.set({},`${this.name}_SelectTxt`,findObj.name)
});
this.$emit("on-change",findObj.value,_.set({},`${this.name}_Select`,findObj))
}
},
created(){
},
computed:{
...mapGetters(['airforce']),
labelWidthComputed () {
const width = this.title.replace(/[^x00-xff]/g, '00').length / 2 + 1
if (width < 10) {
return width + 'em'
}
},
}
}



