
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapActions, mapGetters } from 'vuex'
export default {
name: 'HelloWorld',
...mapActions,
computed: mapGetters({
airforce: 'airforce'
}),
data () {
return {
msg: '租房App项目'
}
},
methods: {
...mapActions(['action']),
goHome(){
this.$router.push("/")
}
},
components: {
Group,
Cell,
XButton
},
created(){
this.action({
moduleName:"s",
goods:"1212"
})
}
}



