
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { ZInput } from "@/components"
import { mapGetters, mapActions } from "vuex"
export default {
name: "login",
components:{
ZInput,
Flexbox, FlexboxItem, XInput, XButton, Group, ZInput },
data(){
return {
Item1:'select',
Item2:'',
isFocus:'',
phone:"",
isWeixin:false
}
},
methods:{
...mapActions(['action']),
loginTab(type){
if(type == "Item1"){
this.Item1 = "select";
this.Item2 = "";
}
if(type == "Item2"){
this.Item2 = "select";
this.Item1 = "";
}
},
getCode(next){
if(!this.airforce.login.phone || this.$utils.is_S(this.airforce.login.phone)){
this.$vux.toast.text("【手机号】不能为空");
return;
}
if(!this.$utils.isPhone(this.airforce.login.phone)){
this.$vux.toast.text("【手机号】格式错误");
return;
}
this.action({
moduleName:"getcode",
url:'api/member/getcode',
method:'post',
isFormData:true,
data:{
phone:this.airforce.login.phone
}
}).then(res=>{
if(res.code != 200){
this.$vux.toast.text(res.message);
return;
}
this.action({
moduleName:"login",
goods:{
code:res.data.code_name
}
});
}).catch(err=>{
this.$vux.toast.text(err);
});
next({
fieldName:"login"
})
},
loginMain(cons){
let data = this.airforce.login;
if(this.$route.query.token){
data = {
token:this.$route.query.token
}
};
let router = "Home";
if(this.$route.query.router){
router =  this.$route.query.router;
};
this.action(Object.assign({
moduleName:"login_post",
method:'post',
isFormData:true,
data:data
},cons)).then(res=>{
if(res.code != 200){
this.$vux.toast.text(res.message);
return;
};
this.$vux.toast.text('登陆成功');
localStorage.login_post = JSON.stringify(res);
this.action({
moduleName:"prefectInfo",
goods:res.data
});
this.$utils.SearchParentKey(this.$parent,"initConfig").value();
this.$router.push("/"+router);
}).catch(err=>{
this.$vux.toast.text(err);
});
},
login(){
this.action({
moduleName:'login',
goods:{
phone:this.phone
}
});
if(!this.airforce.login.phone || this.$utils.is_S(this.airforce.login.phone)){
this.$vux.toast.text("【手机号】不能为空");
return;
}
if(!this.$utils.isPhone(this.airforce.login.phone)){
this.$vux.toast.text("【手机号】格式错误");
return;
}

if(this.Item2 == "select"){
if(!this.airforce.login.code || this.$utils.is_S(this.airforce.login.code)){
this.$vux.toast.text("【短信验证码】不能为空");
return;
}
}else {
if(!this.airforce.login.password || this.$utils.is_S(this.airforce.login.password)){
this.$vux.toast.text("【密码】不能为空");
return;
}
if(this.airforce.login.password.length < 6){
this.$vux.toast.text("【密码】长度至少为6位及以上字符");
return;
}
};
let url = '/member/login';
if(this.Item2 == 'select'){

url = 'adminapi/member/quicklogin';
}
this.$vux.loading.show({text: '登陆中..'});
this.loginMain({
url:url
});
},
go(name){
this.$router.push(name);
},
focus(){
this.isFocus = 'loginTabBox-focus';
},
blur(){
this.isFocus = '';
}
},
computed:{
...mapGetters(['airforce'])
},
mounted(){
var ua = navigator.userAgent.toLowerCase();
this.isWeixin = ua.indexOf('micromessenger') != -1;
if (this.isWeixin) {
this.$vux.loading.show("加载中..");
this.loginMain({
fullUrl:"http://yr.5yaofq.com/index.php?g=WeChat&m=Wechat&a=wxlogin"
});
}else{
this.isWeixin = true;
}
this.plus.then(plus=>{
plus.navigator.setFullscreen( true );
});
}
}



