
import $ from "jquery";
import CellBox from 'vux/src/components/cell-box/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapActions,mapGetters } from 'vuex'

export default {
name: "Wdyhk",
data() {
return {
msg: "我的银行卡",
have: false, //是否有银行卡信息
showmr: false, //显示该卡是否为默认的标志
colorcase: ["a", "b", "c"],
yhklist: [],
ran: 0, //背景色的随机数
styleObject: {},
whether: false, //确认删除的弹窗
delindex: 0 ,//删除银行卡的下标
delitemindex:""
};
},
components: {
CellBox,
Confirm,
Toast
},
methods: {
...mapActions(['action']),
disappear() {

$(".bj").fadeOut();
$(".sz").fadeIn();
},
suredel(t,k) {

this.delindex = t;
this.delitemindex=k;
this.whether = true;
},
del() {

let k = this.delitemindex;
let t = this.delindex;
this.action({
moduleName:"delBank",
url:"adminapi/member/delBank",
method:"post",
data:{
token:this.airforce.login_post.data.token,
id:k
}
}).then((res)=>{
if(res.code==200){
this.$vux.toast.text(res.message);
this.yhklist.splice(t,1);
if(this.yhklist.length==0){
this.have=true;
}
}else{
this.$vux.toast.text(res.message)
}
}).catch((err)=>{
this.$vux.toast.text(err)
})
$(".bj").eq(t) .fadeToggle();
$(".sz").eq(t).fadeIn();
},
mr(t,k) {

this.action({
moduleName:"defaultBank",
url:"adminapi/member/defaultBank",
method:"post",
data:{
token:this.airforce.login_post.data.token,
id:k
}
}).then((res)=>{
if(res.code==200){
this.$vux.toast.text(res.message);
for(let l=0;l<this.yhklist.length;l++){//改变页面上默认的显示
this.yhklist[l].status=false;
}
this.yhklist[t].status=true;
}else{
this.$vux.toast.text(res.message)
}
}).catch((err)=>{
this.$vux.toast.text(err)
})
$(".bj").eq(t).fadeToggle();
$(".sz") .eq(t).fadeIn();
},
sz(t) {//点击出现遮罩层
if (
$(".bj") .eq(t).is(":hidden")
) {
$(".bj").fadeOut();
$(".sz").fadeIn();
$(".bj").eq(t).fadeIn();
$(".sz").eq(t).fadeOut();
} else {
$(".bj").eq(t).fadeOut();
$(".sz").eq(t).fadeIn();
}
}
},
created(){
this.action({
moduleName:"bankList",
url:"adminapi/member/bankList",

method:"post",
data:{
token:this.airforce.login_post.data.token,
}
}).then((res)=>{
if(res.code==200){
for(let i=0;i<res.data.length;i++){//循环改变银行卡号，只显示最后4位
let len=res.data[i].bank.length;
let str=res.data[i].bank.substring(len-4, len);
res.data[i].bank="**** **** **** " + str;
if(res.data[i].status=="2"){
res.data[i].status=true;
}else{
res.data[i].status=false;
}
this.yhklist.push(res.data[i])
}
if(this.yhklist.length==0){
this.have=true;
}
}else{
this.have=true;
this.$vux.toast.text(res.message)
}
}).catch((err)=>{
this.have=true;
this.$vux.toast.text(err)
})

},
computed:{
...mapGetters(["airforce"]),
},
mounted(){
this.action({
moduleName:'layout',
goods:{
clickRight:()=>{
this.$router.push("/Addyhk")
}
}
})
}
};



