
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { mapActions, mapGetters } from "vuex"
export default {
name: "z-card",
components:{ Card, Flexbox, FlexboxItem },
props:{
data:{
type:Object,
default:()=>{},
}
},
data(){
return{
select:false,
img:require('@/assets/img/Login/touxiang.png')
}
},
methods:{
...mapActions(['action']),
clickSelect(){
this.select = !this.select;
},
HousingEditor(conf = {}){
this.action({
moduleName:'HousingEditor',
goods:null
});
this.action({
moduleName:'HousingEditor',
goods:_.merge(this.data,conf)
});
this.$router.push('/HousingEditor');
},
HousingOut(){
this.action({
moduleName:'Housesource_Out_post',
url:'/adminapi/Housesource/changestatus',
method:'post',
data:{
token:this.airforce.login_post.data.token,
status:'0',
id:this.data.id,
}
}).then(res=>{
if(res.code != 200){
this.$vux.toast.text(res.message);
return;
}
this.$vux.toast.text("下架成功");
try {
this.$root.$children[0].reload();
}catch (e){}
}).catch(err=>{this.$vux.toast.text(err)})
}
},
computed:{
...mapGetters(['airforce']),
defaultImg(){
if(this.data.img && this.data.img != 'null'){
try {
let imgnew = null;
for(let k in this.data.img){
imgnew = this.data.img[k];
break;
}
if(imgnew){
return imgnew;
}
return this.img;
}catch (e){
return this.img;
}
}
return this.img;
},
statusList(){
switch(this.data.status){

case '0':
return [
{
icon:'&#xe62a;',
name:"出售",
click:()=>{
this.HousingEditor();
}
},
{
icon:'&#xe62a;',
name:"删除房源",
click:()=>{

this.$vux.confirm.show({
title:"温馨提示",
content:"确定删除该",
onConfirm:()=>{
this.action({
moduleName:'getHousesourceLists_post',
method:'post',
url:'/adminapi/Housesource/delete',
data:_.merge({
token:this.airforce.login_post.data.token,

status:'0',
},{
id:this.data.id
})
}).then(res=>{
if(res.code != 200){
this.$vux.toast.text(res.message);
return;
}
try {
const zCardParent = this.$parent.$parent.$parent.$parent;
const index = _.findIndex(zCardParent.tabs[zCardParent.index].list,{id:this.data.id});
this.select = false;
zCardParent.tabs[zCardParent.index].list.splice(index,1);
}catch (e){}
this.$vux.toast.text("删除成功");
}).catch(err=>{
this.$vux.toast.text(err);
});
}
})
}
},
];
break;

case '1':
return [
{
icon:'&#xe62a;',
name:"房源信息",
click:()=>{

}
},
];
break;

case '2':
return [
{
icon:'&#xe62a;',
name:"房源信息",
click:()=>{
this.$router.push("/HousingDetails?id="+this.data.id+'&o_id='+this.data.o_id);
}
},
{
icon:'&#xe62a;',
name:"修改房源",
click:()=>{
this.HousingEditor({HousingEditorType:2});
}
},
{
icon:'&#xe62a;',
name:"房源下架",
click:()=>{
this.HousingOut();
}
},
];
break;

default:
return [
{
icon:'&#xe62a;',
name:"房源信息",
click:()=>{

}
},
{
icon:'&#xe62a;',
name:"续约",
click:()=>{

}
},
];
break;
}
return [];
}
}
}



