
import { mapActions, mapGetters } from 'vuex'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default {
name: "z-x-dialog",
components:{ XDialog, Cell, Checker, CheckerItem, XInput },
props:{
moduleName:{
type:String,
default:'zXDialog'
},
fieldName:{
type:String,
default:'zXDialog'
},
title:{
type:String,
default:''
},
placeholder:{
type:String,
default:''
},
alert:{
type:String,
default:''
},
},
data(){
return {
show:false,
editorShow:false,
CheckboxList:[1,2,3],
CheckboxMax:[]
}
},
methods:{
...mapActions(['action']),
close(){
this.show = false;
},
editor(){
this.editorShow = true;
},
closeEditor(){
this.editorShow = false;
},
change(val,index){
this.CheckboxList[index] = parseInt(val);
this.CheckboxMax = this.CheckboxMax.filter(e=>{
if(this.CheckboxList.indexOf(e) > -1){
return e;
};
});
},
showDialog(){
this.show = true;
},
add(){
this.CheckboxList.push(this.CheckboxList.length + 1);
}
},
computed:{
...mapGetters(['airforce']),
},
watch:{
show(val){
if(!val){
this.action({
moduleName:this.moduleName,
goods:_.set({},this.fieldName,null)
});
this.action({
moduleName:this.moduleName,
goods:_.set({},this.fieldName,this.CheckboxMax.filter(e=>e != 'add'))
});
this.editorShow = false;
}
}
}
}



